import React from "react";
import styled, { css } from "styled-components";

export const FramePositions = {
  TopRight: {
    top: "-10px",
    right: "-10px",
  },
  TopLeft: {
    top: "-10px",
    left: "-10px",
  },
  BottomRight: {
    bottom: "-10px",
    right: "-10px",
  },
  BottomLeft: {
    bottom: "-10px",
    left: "-10px",
  },
};

export const FramedPicture = ({
  children,
  color = "#4449e4",
  position = FramePositions.TopRight,
  ...props
}) => {
  return (
    <PictureWrapper color={color} position={position} {...props}>
      {children}
    </PictureWrapper>
  );
};

const PictureWrapper = styled.div`
  position: relative;

  &::before {
    content: "";
    background: ${({ color }) => color};
    position: absolute;
    width: 75%;
    height: 90%;
    ${({ position }) => css(position)}
  }
  & > * {
    isolation: isolate;
  }
`;
